<template>
  <van-page>
    <van-nav-bar title="AES-256-GCM" />
    <div class="button-area">
      <van-button type="primary" @click="encrypt">Encrypt</van-button>
    </div>

    <van-field v-model="plaintext" label="Plaintext" type="textarea" rows="4" placeholder="Enter plaintext">
      <template #button>
        <van-button size="mini" @click="copyText(plaintext)">Copy</van-button>
      </template>
    </van-field>

    <van-field v-model="key" label="Key" type="text" placeholder="Enter any string as key" />

    <van-field v-model="ciphertext" label="Ciphertext" type="textarea" rows="4" placeholder="Encrypted result">
      <template #button>
        <van-button size="mini" @click="copyText(ciphertext)">Copy</van-button>
      </template>
    </van-field>

    <div class="button-area">
      <van-button type="primary" @click="decrypt">Decrypt</van-button>
    </div>

    <van-cell>
      <div>
        <span style="font-weight: bold;">AES-256-GCM：</span>
        <div class="use-cases">
          1. Efficiently encrypts data of arbitrary length, from short messages to large files;<br />
          2. Provides encryption, authentication, and integrity verification in one step;<br />
          3. Uses a 96-bit (12-byte) initialization vector (IV) to ensure unique encryption instances;<br />
          4. Employs a 256-bit key for strong cryptographic security;<br />
          <!-- 5. Supports additional authenticated data (AAD) for flexible authentication without encryption.<br /> -->
        </div>
        
        
        <br />
      </div>

      
    </van-cell>

  </van-page>

  <div>&nbsp;</div>
  <div>&nbsp;</div>
  <div>&nbsp;</div>
  <div>&nbsp;</div>
  <div>&nbsp;</div>
  <div>&nbsp;</div>
  <div>&nbsp;</div>

  <TabBar />

</template>

<script setup>
import { ref } from 'vue';
import { Toast } from 'vant';
import TabBar from '@/components/TabBar.vue'; // 确保路径正确

const plaintext = ref('');
const key = ref('');
const ciphertext = ref('');

// Base64 helpers
const toBase64 = (buffer) => btoa(String.fromCharCode(...new Uint8Array(buffer)));
const fromBase64 = (b64) => Uint8Array.from(atob(b64), c => c.charCodeAt(0));

// Derive 256-bit key using SHA-256
async function deriveKey(password) {
  const encoder = new TextEncoder();
  const data = encoder.encode(password);
  const hash = await crypto.subtle.digest('SHA-256', data);
  return await crypto.subtle.importKey('raw', hash, 'AES-GCM', false, ['encrypt', 'decrypt']);
}

async function encrypt() {
  try {
    const keyObj = await deriveKey(key.value);
    const iv = crypto.getRandomValues(new Uint8Array(12)); // 96-bit IV
    const encoded = new TextEncoder().encode(plaintext.value);
    const encrypted = await crypto.subtle.encrypt(
      { name: 'AES-GCM', iv },
      keyObj,
      encoded
    );
    const combined = new Uint8Array(iv.length + encrypted.byteLength);
    combined.set(iv);
    combined.set(new Uint8Array(encrypted), iv.length);
    ciphertext.value = toBase64(combined.buffer);
    Toast('Encrypted!');
  } catch (e) {
    Toast.fail('Encryption failed');
    console.error(e);
  }
}

async function decrypt() {
  try {
    const keyObj = await deriveKey(key.value);
    const data = fromBase64(ciphertext.value);
    const iv = data.slice(0, 12);
    const encrypted = data.slice(12);
    const decryptedBuffer = await crypto.subtle.decrypt(
      { name: 'AES-GCM', iv },
      keyObj,
      encrypted
    );
    plaintext.value = new TextDecoder().decode(decryptedBuffer);
    Toast('Decrypted!');
  } catch (e) {
    Toast.fail('Decryption failed');
    console.error(e);
  }
}

function copyText(text) {
  navigator.clipboard.writeText(text).then(() => Toast('Copied!'));
}
</script>

<style scoped>
.button-area {
  margin: 16px 0;
  display: flex;
  justify-content: center;
}
</style>
