<template>

  <van-page>
    <van-nav-bar title="SHA-256" />
    <van-cell-group>
      <!-- Input text -->
      <van-field v-model="input" label="Text" placeholder="Enter text to hash..." rows="5" type="textarea" />

      <!-- Input hash salt -->
      <van-field v-model="salt" label="Hash Salt" placeholder="Enter random string (optional)" type="textarea" />

      <!-- Calculate hash button -->
      <!-- 使用 Flexbox 容器保持按钮的上下位置不变并水平居中 -->
      <div class="button-container">
        <van-button type="primary" @click="calculateHash">Calculate Hash</van-button>
      </div>
      <!-- Display input text and hash -->
      <van-field v-model="inputAndSalt" label="Input + Salt" type="textarea" readonly>
        <template #button>
          <van-button size="mini" @click="copyTextToClipboard">Copy</van-button>
        </template>
      </van-field>

      <van-field v-model="hash" label="Hash Value" type="textarea" readonly>
        <template #button>
          <van-button size="mini" @click="copyHashToClipboard">Copy</van-button>
        </template>
      </van-field>
    </van-cell-group>

    <van-divider />

    <!-- Scene description -->

    <van-cell>
      <div style="width: 100%;">
        <span style="font-weight: bold;">SHA-256</span>
        <div class="use-cases">
          1. Converting text of any length into a fixed-length string;<br />
2. Verifying data integrity to ensure that content has not been altered.
        </div>
      </div>
    </van-cell>


    <van-divider />

  </van-page>
  <div>&nbsp;</div>
  <div>&nbsp;</div>
  <div>&nbsp;</div>
  <div>&nbsp;</div>
  <div>&nbsp;</div>
  <div>&nbsp;</div>
  <div>&nbsp;</div>

  <TabBar />

</template>


<script setup>
import { ref } from 'vue';
import { sha3_256 } from 'js-sha3'; // 本地引入 SHA3-256 函数
import TabBar from '@/components/TabBar.vue';

const input = ref('');
const salt = ref('');
const inputAndSalt = ref('');
const hash = ref('');

const calculateHash = () => {
  inputAndSalt.value = input.value.trim() + salt.value.trim();
  hash.value = sha3_256(inputAndSalt.value);
};

const copyTextToClipboard = () => {
  navigator.clipboard.writeText(inputAndSalt.value).then(() => {
    alert('Text copied');
  });
};

const copyHashToClipboard = () => {
  navigator.clipboard.writeText(hash.value).then(() => {
    alert('Hash copied');
  });
};
</script>



<style scoped>
.van-page {
  padding: 16px;
}

.use-cases {
  text-align: left;
  /* Align text to the left */
}
</style>
