<template>
  <van-page>
    <van-nav-bar title="HMAC-SHA-256" />
    <van-cell-group>
      <!-- Input key -->
      <van-field v-model="key" label="Key" placeholder="Enter secret key..." type="textarea" rows="2" />

      <!-- Input message -->
      <van-field v-model="message" label="Message" placeholder="Enter message to hash..." type="textarea" rows="5" />

      <!-- Calculate HMAC button -->
      <div class="button-container">
        <van-button type="primary" @click="calculateHMAC">Calculate HMAC</van-button>
      </div>

      <!-- Display message -->
      <!-- <van-field v-model="message" label="Message" type="textarea" readonly>
        <template #button>
          <van-button size="mini" @click="copyMessageToClipboard">Copy Message</van-button>
        </template>
      </van-field> -->

      <!-- Display HMAC -->
      <van-field v-model="hmac" label="HMAC Value" type="textarea" readonly>
        <template #button>
          <van-button size="mini"  @click="copyText(hmac)">Copy</van-button>
        </template>
      </van-field>
    </van-cell-group>

    <van-divider />

    <!-- Scene description -->
    <van-cell>
      <div style="width: 100%;">
        <span style="font-weight: bold;">HMAC-SHA-256</span>
        <div class="use-cases">
          1. Verify both authenticity and integrity of a message;<br />
          2. Secure API requests by attaching an HMAC to ensure the request hasn’t been tampered with.
        </div>
      </div>
    </van-cell>

    <van-divider />
  </van-page>
  <div>&nbsp;</div>
  <div>&nbsp;</div>
  <div>&nbsp;</div>
  <div>&nbsp;</div>
  <div>&nbsp;</div>
  <div>&nbsp;</div>
  <div>&nbsp;</div>

  <TabBar />
</template>

<script setup>
import { ref } from 'vue';
import { Toast } from 'vant';
import TabBar from '@/components/TabBar.vue'; // 确保路径正确
import CryptoJS from 'crypto-js';

const key = ref('');
const message = ref('');
const hmac = ref('');

const calculateHMAC = () => {
  if (!key.value.trim() || !message.value.trim()) {
    alert('Please enter both a key and a message.');
    return;
  }
  hmac.value = CryptoJS.HmacSHA256(message.value.trim(), key.value.trim()).toString(CryptoJS.enc.Hex);
};


function copyText(text) {
  navigator.clipboard.writeText(text).then(() => Toast('Copied!'));
}

</script>

<style scoped>
.van-page {
  padding: 16px;
}

.use-cases {
  text-align: left;
}
</style>